.checkbox-unit {
    font-size: 14px;
    line-height: 16px;
    font-family: 'Times New Roman', Times, serif;
    padding: 4px;
    margin: 0;
    display: flex;
    align-items: baseline;
    width: 80%;
    height: auto;
}

.checkbox-unit-container {
    height: 100%;
    display: flex;
}

.checkbox-unit__checkbox {
    position: absolute;
    z-index: -1;
    opacity: 0;
}

.checkbox-unit__checkbox+label {
    display: inline-flex;
    align-items: center;
    user-select: none;
}

.checkbox-unit__checkbox+label::before {
    content: '';
    display: inline-block;
    width: 1em;
    height: 1em;
    flex-shrink: 0;
    flex-grow: 0;
    border: 1px solid #adb5bd;
    border-radius: 0.25em;
    margin-right: 0.5em;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%;
}

.checkbox-unit__checkbox:checked+label::before {
    border-color: #0b76ef;
    background-color: #0b76ef;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
}

.checkbox-checked {
    color: #888888;
    text-decoration: line-through;
}

.checkbox-text {
    align-self: flex-end;
}